import React, { Component } from 'react';
import { Table } from 'antd';
import { setTranscriptEachStudent } from 'dump/data';

const columns = [
  {
    title: 'Ký hiệu',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Tên học phần',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Số tín chỉ',
    dataIndex: 'numberCredits',
    key: 'numberCredits',
  },
  {
    title: 'Điểm lần 1',
    dataIndex: 'firstScore',
    key: 'scores1',
  },
  {
    title: 'Điểm cao nhất',
    dataIndex: 'highestScore',
    key: 'highestScore',
  },
  {
    title: 'Điểm số',
    dataIndex: 'Score',
    key: 'Score',
  },
  {
    title: 'Điểm chữ',
    dataIndex: 'wordScore',
    key: 'wordScore',
  }
];

class TableTranscriptEachStudent extends Component {
  state = {}
  render() {
    return <Table bordered dataSource={setTranscriptEachStudent} columns={columns} />
  }
}

export default TableTranscriptEachStudent;



